let state = {
  experience: {
    type: 'image',
    src: 'static/experience/image.png'
  },
  waiting: false
}

let mutations = {
  waiting: function (state, status) {
    state.waiting = status
  }
}

let actions = {
  init: function (context) {
  },
  waiting: function (context, status) {
    context.commit('waiting', status)
  }
}

export default {
  state,
  mutations,
  actions
}



