<template>
  <div class="hello">
    <a-scene embedded arjs='trackingMethod: best;'>

        <%--<a-marker-camera preset='custom' url='https://picspeanutbutter.azurewebsites.net/PICSstar.patt'></a-marker-camera>
        --%>
        <a-assets>
     <img id='earth' src='https://raw.githubusercontent.com/aframevr/sample-assets/master/assets/images/space/earth_atmos_4096.jpg'/>
     <img id='PicPic' src='/static/img/pics.jpg'/>
</a-assets>
   <a-anchor hit-testing-enabled='true'>

          <a-entity id="jim"
     geometry="primitive: box; depth: 1; height: 1; width: 1"
     material='src: #PicPic;'
     ring-on-beat="analyserEl: #analyser"
     position="0 0.25 0">
                <a-animation attribute="rotation"
            dur="7000"
            to="0 360 0"
            easing= "linear"
            repeat="indefinite">
     </a-animation>

          </a-entity>



</a-anchor>
<a-camera-static/>
</a-scene>
    <div class="modal fade" id="questions" tabindex="-1" role="dialog" aria-labelledby="questionsLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="questionsLabel">We hope you enjoyed your...</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            ...
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary">Save changes</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'hello',
  data () {
    return {
      msg: 'We have a static site'
    }
  },
  computed: mapState([
    'user'
  ]),
  created () {
    this.$store.dispatch('init')
  },
  methods: {
    answer: function (answer) {
      this.$store.dispatch('answerQuestion', {answer})
    }
  }
}
</script>
<style>
</style>



