<template>
  <div>
    <h2>Please tell us a little bit about you</h2>
    <hr></hr>
    <div class="form-group">
      <label for="gender">Your gender:</label>
      <select class="form-control" id="gender" v-model="gender" aria-describedby="genderHelp">
        <option value="0">I'd rather not say</option>
        <option value="1">Male</option>
        <option value="2">Female</option>
        <option value="3">Something else</option>
      </select>
      <small id="genderHelp" class="form-text text-muted">Because we all all different beasts</small>
    </div>
    <div class="form-group">
      <label for="age">Your Age</label>
      <select class="form-control" id="age" v-model="age" aria-describedby="ageHelp">
        <option value="0">Select your age group</option>
        <option value="1">less than 10</option>
        <option value="2">10 to 20</option>
        <option value="3">20 to 40</option>
        <option value="4">more than 40</option>
      </select>
      <small id="ageHelp" class="form-text text-muted">So we can tailor to your age group</small>
    </div>
    <div class="form-group">
      <label for="email">Email address</label>
      <input type="email" class="form-control" id="email" v-model="email" aria-describedby="emailHelp" placeholder="Your email">
      <small id="emailHelp" class="form-text text-muted">We'll never share your email with anyone else.</small>
    </div>
    <b-button variant="primary" v-on:click="answer">Continue</b-button>
  </div>
</template>

<script>
export default {
  name: 'setup',
  data () {
    return {
      age: 0,
      gender: 0,
      email: null
    }
  },
  created () {
  },
  methods: {
    answer: function (answer) {
      this.$store.dispatch('setupUser', { answers: {
        age: parseInt(this.age),
        gender: parseInt(this.gender),
        email: this.email
      }})
    }
  }
}
</script>
<style>
</style>



