// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
// import App from './App'
import Vuex from 'vuex'
import router from '@/router'
import BootstrapVue from 'bootstrap-vue'
import localDatabase from './state/local'
import StateInterface from './state/modules/ui'
import StateUser from './state/modules/user'

Vue.use(Vuex)
Vue.use(BootstrapVue)

/* eslint-disable no-new */
// new Vue({
//   el: '#app',
//   router,
//   template: '<App/>',
//   components: { App }
// })

var cb = function () {
  let store = new Vuex.Store({
    strict: true,
    modules: {
      ui: StateInterface,
      user: StateUser
    }
    // middlewares: [
    //   Logger()
    // ]
  })

  Vue.config.productionTip = false
  Vue.config.devtools = true
  new Vue({
    router,
    store
  }).$mount('#app')
  // new Vue({
  //   el: '#app',
  //   router,
  //   store
  // })
  // new Vue({
  //   el: '#app',
  //   router,
  //   store,
  //   template: '<App/>',
  //   components: { App }
  // })
}
localDatabase.init(cb)



