export default {
  app: 'can-ar',
  token: 'dfa4t0ufshudvv0s0',
  state: {
    version: 3,
    commonDB: {
      key: 'id',
      index: [
        { index: 'key', path: 'key', unique: false }
      ]
    },
    datatypes: {
      experience: {
        url: 'https://can-ar.server/experience',
        dataModel: {
          id: {
            dataType: 'number'
          },
          type: {
            dataType: 'number'
          },
          srcUrl: {
            dataType: 'string'
          }
        },
        index: [
          { index: 'type', path: 'data.type', unique: false }
        ]
      },
      user: {
        url: 'https://can-ar.server/api',
        dataModel: {
          id: {
            dataType: 'number'
          },
          type: {
            dataType: 'number'
          },
          srcUrl: {
            dataType: 'string'
          }
        },
        index: [
          { index: 'type', path: 'data.type', unique: false }
        ]
      }
    }
  }
}



