import Vue from 'vue'
import Router from 'vue-router'
import Arpage from '@/components/Arpage'
import Setup from '@/components/Setup'
import Layout from '@/components/Layout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Layout,
      children: [
        { path: 'setup', component: Setup },
        { path: '', redirect: 'ar' }
      ]
    },
    {
      path: '/ar',
      component: Arpage
    }
  ]
})



